import React,{Component} from 'react';
import axios from 'axios';
import ViewBooking from './bookingDisplay';

const bookurl = "https://edumot.herokuapp.com/orders";


class Booking extends Component{
    constructor(){
        super()

        this.state={
            orders:''
        }
    }

    render(){
        console.log("class",this.props)
        return(
            <div className="container">
                <ViewBooking orderdata={this.state.orders}/>
            </div>
        )
    }


    async componentDidMount(){
        let response =  await axios.get(`${bookurl}`)
        this.setState({orders:response.data})
    }


    /*componentDidMount(){
        axios.get(`${bookurl}`)
        .then((response) =>  {this.setState({hotelist:response.data})})
    }*/

}

export default Booking;