import React, { useRef, useState } from 'react';
import './App.css';

import firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/auth';
import 'firebase/analytics';

import { useAuthState } from 'react-firebase-hooks/auth';
import { useCollectionData } from 'react-firebase-hooks/firestore';

firebase.initializeApp({
  apiKey: "AIzaSyBb-L7zWRJ5-10oxL7YHE3NPRCNFV1Kf4o",
  authDomain: "chat-room-fbea0.firebaseapp.com",
  databaseURL: "https://chat-room-fbea0.firebaseio.com",
  projectId: "chat-room-fbea0",
  storageBucket: "chat-room-fbea0.appspot.com",
  messagingSenderId: "478322489472",
  appId: "1:478322489472:web:5c173dbbae63aaf6480e65",
  measurementId: "G-08RWT85XC3"
})

const auth = firebase.auth();
const firestore = firebase.firestore();
const analytics = firebase.analytics();


function App() {

  const [user] = useAuthState(auth);
 ////////Chat Heading and SignIn SignOut
  return (
    <div className="App">
      <section>
      <div>
        <h1 className="heading">Message Us</h1>
        <SignOut/>
      </div>
      <div>
        {user ? <ChatRoom /> : <SignIn />}
      </div>
      </section>

    </div>
  );
}

function SignIn() {

  const signInWithGoogle = () => {
    const provider = new firebase.auth.GoogleAuthProvider();
    auth.signInWithPopup(provider);
  }

  return (
    <>
      <button className="btn btn-danger" onClick={signInWithGoogle}>Sign in with Google</button><br/>
      <h4 className="msg-info">Do not violate the community guidelines <br/> or you will be banned for life!</h4>
    </>
  )

}

function SignOut() {
  return auth.currentUser && (
    <button className="sign-out" onClick={() => auth.signOut()}>Sign Out</button>
  )
}

/////Chatroom Data Fetch

function ChatRoom() {
  const dummy = useRef();
  const messagesRef = firestore.collection('messages');
  const query = messagesRef.orderBy('createdAt').limit(25);

  const [messages] = useCollectionData(query, { idField: 'id' });

  const [formValue, setFormValue] = useState('');


  const sendMessage = async (e) => {
    e.preventDefault();

    const { uid, photoURL } = auth.currentUser;

    await messagesRef.add({
      text: formValue,
      createdAt: firebase.firestore.FieldValue.serverTimestamp(),
      uid,
      photoURL
    })

    setFormValue('');
    dummy.current.scrollIntoView({ behavior: 'smooth' });
  }




 ////Chat Box Messages
  return (<>
    <div className="message-box">

      {messages && messages.map(msg => <ChatMessage key={msg.id} message={msg} />)}

      <span ref={dummy}></span>

    </div>

    <form onSubmit={sendMessage} className="input-box">

      <input type="text" className="typehere" value={formValue} onChange={(e) => setFormValue(e.target.value)} placeholder="say something nice" />

      <button className="send-button" type="submit" disabled={!formValue}>send</button>

    </form>
  </>)
}


function ChatMessage(props) {
  const { text, uid, photoURL } = props.message;

  const messageClass = uid === auth.currentUser.uid ? 'sent' : 'received';


  /////Chatbox data messages and photo 
  return (<>
    <div className={`message ${messageClass}`}>
      <img className="pic" src={photoURL} alt="google-pic" />
      <p className="show-text">{text}</p>
    </div>
  </>)
}


export default App;
