import React from 'react';
import './Socialicons.css';

const Socialicons = () => {
    return(
        <React.Fragment>
            <div className="social-icons">
                <a href="https://www.facebook.com/" target="_blank" rel="noopener noreferrer"><img src="https://i.ibb.co/0DnsTYn/facebook.png" alt="facebook"/></a>
                <a href="https://www.instagram.com/" target="_blank" rel="noopener noreferrer"><img src="https://i.ibb.co/8dcM7Dp/instagram.png" alt="instagram"/></a>
                <a href="https://www.linkedin.com/" target="_blank" rel="noopener noreferrer"><img src="https://i.ibb.co/C9LqTXG/linkedin.png" alt="linkedin"/></a>
                <a href="https://www.twitter.com/" target="_blank" rel="noopener noreferrer"><img src="https://i.ibb.co/7XBzQxz/twitter.png" alt="twitter"/></a>
            </div>
        </React.Fragment>
    )
}

export default Socialicons;