import React  from 'react';
import {Link} from 'react-router-dom';
import './Listing.css';


const ListingDisplay = (props) => {

    const renderList = ({listdata}) => {
        if(listdata){
            return listdata.map((item) => {
                return(
                    <Link to={`/details/${item._id}`}>
                        <div class="container">
                            <div class="item-container" key={item._id}>
                                <img className="rest-image" src={item.thumb} alt=""/>
                                <div class="content">
                                    <div class="hotel_name">
                                    <Link to={`/details/${item._id}`}>{item.name}</Link>
                                    </div>
                                    <div class="city_name">
                                        {item.city_name}
                                    </div>
                                    <div class="city_name">
                                        {item.locality}
                                    </div>
                                    <div class="city_name">
                                        {item.address}
                                    </div>
                                    <div className="city_name">
                                        <span className="glyphicon glyphicon-glass"></span>
                                        <span className="glyphicon glyphicon-cutlery"></span>
                                        <i className="fas fa-smoking-ban"></i>
                                    </div>
                                    <h4 class="cuisine">
                                        {item.Cuisine[0].name} | {item.Cuisine[1].name}
                                    </h4>
                                    <div class="price-container">
                                        <div class="price">Rs. <strong>{item.cost}</strong></div>
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                    </Link>
                )
            })
        }else{
            return(
                <div>
                    <center>
                    <img src="/images/loading.gif" alt="gif"/>
                    </center>
                </div>
            )
        }
    }
    return(
        <div>
            <div className="row">
                
                <div className="col-md-12">
                    {renderList(props)}
                </div>
            </div>
        </div>
    )
}

export default ListingDisplay;