import React from 'react';
import {Link} from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import './RestaurentDisplay.css';

const RestaurentList = (props) => {
    const renderHotel = ({restData})=>{
        if(restData){
            return(
                <div className="container">
                    <div className="panel panel-primary">
                        <div className="panel-heading">
                            <h3>{restData.name.toUpperCase()}</h3>
                        </div>
                        <div className="panel-body">
                            <div className="row">
                                <div className="col-md-12">
                                    <button type="button" data-toggle="modal" data-target="#myModal">
                                        <img className="img-responsive" src={restData.thumb}
                                        style={{width:1600,height:400}} alt=""/>
                                    </button>
                                    <div id="myModal" className="modal fade" role="dialog">
                                        <div className="modal-dialog">
                                            <div className="modal-content">
                                                <div className="modal-header">
                                                    <button type="button" className="close" data-dismiss="modal">&times;</button>
                                                    <h4 className="modal-title">
                                                        Image Gallery For {restData.name}
                                                    </h4>
                                                </div>
                                                <div className="modal-body">
                                                    <div id="myCarousel" className="carousel slide" data-ride="carousel">

                                                        <ol className="carousel-indicators">
                                                            <li data-target="#myCarousel" data-slide-to="0" className="active"></li>
                                                            <li data-target="#myCarousel" data-slide-to="1"></li>
                                                            <li data-target="#myCarousel" data-slide-to="2"></li>
                                                        </ol>
                                                        <div className="carousel-inner">
                                                            <div className="item active">
                                                                <img className="img-responsive" src={restData.thumb} style={{width:1600,height:400}} alt=""/>
                                                            </div>

                                                            <div className="item">
                                                                <img className="img-responsive" src={restData.thumb} style={{width:1600,height:400}} alt=""/>
                                                            </div>

                                                            <div className="item">
                                                                <img className="img-responsive" src={restData.thumb} style={{width:1600,height:400}} alt=""/>
                                                            </div>
                                                        </div>
                                                        <a className="left carousel-control" href="#myCarousel" data-slide="prev">
                                                            <span className="glyphicon glyphicon-chevron-left"></span>
                                                            <span className="sr-only">Previous</span>
                                                        </a>
                                                        <a className="right carousel-control" href="#myCarousel" data-slide="next">
                                                            <span className="glyphicon glyphicon-chevron-right"></span>
                                                            <span className="sr-only">Next</span>
                                                        </a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-12">
                                    <h3 className="rest-name">{restData.name}</h3>
                                    <h3 className="rest-locality">{restData.locality}</h3>
                                    <h3 className="address">{restData.address}</h3>
                                    <h4 className="cost"> Rs: {restData.cost}</h4>
                                </div>
                            </div>
                            <div className="row">
                                <Tabs>
                                    <TabList>
                                    <Tab>About</Tab>
                                    <Tab>Contact</Tab>
                                    </TabList>
                                    <TabPanel>
                                        <p>{restData.name}</p>
                                        <p>{restData.type[0].name} | {restData.type[1].name}</p>
                                        <p>{restData.Cuisine[0].name} | {restData.Cuisine[1].name}</p>
                                    </TabPanel>
                                    <TabPanel>
                                        <p>{restData.address}</p>
                                        <p> Phone: {restData.contact_number}</p>
                                    </TabPanel>
                                </Tabs>
                            </div>
                            <div className="row">
                                <Link to={`/list/${sessionStorage.getItem('mealid')}`} className="btn btn-danger">Back</Link>&nbsp;
                                <Link to={`/booking/${restData._id}`} className="btn btn-success">Place Order</Link>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }else{
            return(
                <div>
                    <center>
                    <img src="/images/loading.gif" alt=""/>
                    </center>
                </div>
            )  
        }
    }
    return(
        <React.Fragment>
           {renderHotel(props)}
        </React.Fragment>
    )
}

export default RestaurentList;